<template>
    <views class="point-detail">
        <views
            v-if="list.length === 0 && !loadingData"
            class="point-detail-empty">
            <views class="point-detail-empty-inner">
                <images :src="emptyImg"></images>
                <views class="title">暂无明细</views>
            </views>
        </views>
        <views
            v-else
            v-for="(item, index) in list"
            :key="index"
            class="point-detail-list">
            <views class="point-detail-list-l">
                {{ item.source }}<views class="count">+{{ item.point }}</views>积分
            </views>
            <views class="point-detail-list-r">2019-05-2013:26</views>
        </views>
    </views>
</template>

<script>
import tools from '@/mixins/tools'
import api from '@/utils/api'
export default {
    mixins: [tools],
    data() {
        return {
            list: [],
            loadingData: false,
            emptyImg: require('@/assets/images/empty.png')
        }
    },
    created() {
        this.getPointDetailList()
    },
    methods: {
        getPointDetailList() {
            this.loadingData = true
            api.getPointDetailList().then((res) => {
                this.list = res.list
                this.loadingData = false
            }).catch(() => {
                this.loadingData = false
            })
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
