<template>
    <views>
        <views class="point-source-total">
            <views>
                <views class="p-box">
                    <images :src="pointBg"></images>
                    <views class="count">{{ total }}</views>
                </views>
                <views class="p-title">我的积分</views>
            </views>
        </views>
        <views
            v-for="(item, index) in list"
            :key="index"
            class="point-source-list">
            <views class="title">{{ item.source }}</views>
            <views class="count">+{{ item.point }}</views>
        </views>
    </views>
</template>

<script>
import tools from '@/mixins/tools'
import api from '@/utils/api'
export default {
    mixins: [tools],
    data() {
        return {
            list: [],
            pointBg: require('@/assets/images/point_bg.png')
        }
    },
    computed: {
        total() {
            let total = 0
            this.list.map((item) => {
                total += item.point
            })
            return total
        }
    },
    created() {
        this.getPointSourceList()
    },
    methods: {
        getPointSourceList() {
            api.getPointSourceList().then((res) => {
                this.list = res.list
            })
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
